<template lang="pug">
  .screen(
    :style = "{ 'background-image': bg }")
    transition(name="slide" appear)
      .screen__content(v-if = 'true')
        form.screen__form.form
          main-title.screen__title
            | Soybean Oil Trivia
          .form__item
            input.form__input(
                :class="{ focus: !!user.firstName, 'is-danger focus': errors.has('firstName') }"
                v-validate="'required'"
                type="text"
                name="firstName"
                v-model="user.firstName"
              )
            label.form__label
              | First Name
              sup *
            span.form__error.help-message(v-show="errors.has('firstName')")
              | Field is required
          .form__item
            input.form__input(
                :class="{ focus: !!user.lastName, 'is-danger focus': errors.has('lastName') }"
                v-validate="'required'"
                type="text"
                name="lastName"
                v-model="user.lastName"
              )
            label.form__label
              | Last Name
              sup *
            span.form__error.help-message(v-show="errors.has('lastName')")
              | Field is required
          .form__item
            input.form__input(
                :class="{ focus: !!user.title }"
                type="text"
                name="title"
                v-model="user.title"
              )
            label.form__label
              | Title
          .form__item
            input.form__input(
                :class="{ focus: !!user.email, 'is-danger focus': errors.has('email') }"
                v-validate="'required|email'"
                type="text"
                name="email"
                v-model="user.email"
              )
            label.form__label
              | Email
              sup *
            span.form__error.help-message(v-show="errors.has('email')")
              | Field must be a valid email
          .form__item
            masked-input.form__input(
                :class="{ focus: !!user.phone, 'is-danger focue': !isValidPhone }"
                type="tel"
                name="phone"
                v-model="user.phone"
                @input="checkPhone"
                mask="(111) 111-1111"
              )
            label.form__label
              | Phone
            span.form__error.help-message(v-show="!isValidPhone")
              | Enter your phone number.
          .form__item.form__item--select
            v-select.custom-select(
              :class="{ focus: user.industry, 'is-danger': !validSelect }"
              v-model = "user.industry"
              :options = "options"
              :searchable = "false"
            )
            label(for="industry")
              | Select Industry
              sup *
            span.form__error.help-message(v-show="errors.has('industry')")
              | Field is required
          .form__item
            input.form__input(
                :class="{ focus: !!user.company, 'is-danger focus': errors.has('company') }"
                v-validate="'required'"
                type="text"
                name="company"
                v-model="user.company"
              )
            label.form__label
              | Company
              sup *
            span.form__error.help-message(v-show="errors.has('company')")
              | Field is required
          custom-button(type="button" :disabled="!isValid" @click="submit")
              | Submit
</template>

<script>
  import CustomButton from '@/components/CustomButton';
  import MainTitle from '@/components/MainTitle';
  import CustomInput from '@/components/CustomInput';
  import vSelect from 'vue-select';
  import VeeValidate from 'vee-validate';
  import MaskedInput from 'vue-masked-input';
  import Vue from 'vue';

  Vue.use(VeeValidate);

  export default {
    name: 'registration',
    data() {
      return {
        options: ['Agriculture', 'Foodservice', 'Food Manufacturing', 'Health and Media'],
        validSelect: true,
        isValidPhone: true,
      };
    },
    components: {
      CustomButton,
      MainTitle,
      CustomInput,
      vSelect,
      MaskedInput,
    },
    computed: {
      bg() {
        const url = require('../assets/1_Image-for-Widget-and-Screen-1.jpg');
        return `url(${url})`;
      },
      user() {
        return this.$store.state.user;
      },
      isValid() {
        return this.errors.items.length === 0 && this.isValidPhone && this.user.firstName && this.user.lastName && this.user.email && this.user.industry && this.user.company;
      },
    },
    methods: {
      submit() {
        sessionStorage.setItem('user', JSON.stringify(this.user));
        this.$store.commit('setUser', this.user);
        this.$router.push('/how-to-play');
      },
      checkPhone() {
        const val = arguments[0];
        if (val.match('_')) {
          this.isValidPhone = false;
        } else {
          this.isValidPhone = true;
        }
      },
    },
  };
</script>
<style lang="scss">

  .v-select.custom-select {
    .open-indicator {
      display: none !important;
    }
    .dropdown-toggle {
      border: none;
    }
    .selected-tag {
      display: inline-block;
      background-color: transparent;
      border: none;
      color: #ffffff;
    }
    .form-control {
      padding: 10px 0;
    }
    .dropdown-menu {
      max-width: 300px;
      border-radius: 10px;
      overflow: visible;
      top: calc(100% - 5px);
      border: none;
      padding: 0;

      li {
        &:first-child {
          border-top-left-radius: 9px;
          border-top-right-radius: 9px;
          overflow: hidden;
        }
        &:last-child {
          border-bottom-left-radius: 9px;
          border-bottom-right-radius: 9px;
          overflow: hidden;
        }
        a {
          padding: 10px;
          &:hover {
            background-color: none !important;
          }
        }
      }

      li:not(:last-child) {
        border-bottom: 1px solid #e2e2e2;
      }

      &:before {
        position: absolute;
        content: '';
        width: 15px;
        height: 15px;
        border-radius: 3px;
        top: -7px;
        transform: rotate(45deg);
        left: 50px;
        background-color: white;
        z-index: -1;
      }
    }
  }
</style>

<style lang="scss" scoped>
  .screen {
    &__form {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      height: 100%;
    }
  }
  .form {
    &__item {
      position: relative;
      margin-top: 20px;
      margin-top: 15px;

      &--select {
        margin-top: 40px;
        z-index: 2;
      }
    }

    &__input {
      position: relative;
      border: none;
      width: 100%;
      background-color: transparent;
      border-bottom: 1px solid rgba(#ffffff, .3);
      outline: none;
      border-radius: 0;
      color: #ffffff;
      font-weight: 300;
      padding: 5px 0;
      z-index: 1;

      &.is-danger {
        color: orange;
        border-color: orange;
      }

      &:focus,
      &.focus {
        + .form__label {
          font-size: 12px;
          transform: translateY(-100%);
          top: 0;
        }
      }
    }

    &__label {
      position: absolute;
      transition: .3s;
      top: 50%;
      transform: translateY(-50%);
      left: 0;
      font-weight: 300;
    }

    &__error {
      position: absolute;
      font-size: 12px;
      top: 100%;
      left: 0;
      color: orange;
      margin-top: 5px;
    }
  }
  .v-select {
    position: relative;
    border: none;
    width: 100%;
    background-color: transparent;
    border-bottom: 1px solid rgba(#ffffff, .3);
    border-radius: 0;
    outline: none;
    color: #ffffff;
    font-weight: 300;
    padding: 10px 0;
    z-index: 1;
    appearance: none;

    &:focus,
    &.focus {
      + label {
        font-size: 12px;
        trnasform: translateY(-100%);
        top: -5px;
      }
    }
  }

  label {
    position: absolute;
    transition: .3s;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    font-weight: 300;
  }
  .custom-select {
    z-index: 1;
  }
  .selected-tag {

  }
  .dropdown-toggle {
    border: none;
  }
</style>



